<template>
	<view class="popupContent" @touchmove.stop v-if="value"  >
		<view class="popupContent-title">
			<view-head backColor="#f0f0f0">
				<view class="popupContent-tilte-content" >
					<button type="default" class="iconfont icon-shanchu" @click="clearClick"></button>
					<text class="popupContent-tilte-content-name">{{name}}</text>
					<!-- #ifdef H5 -->
					<button type="default" class="popupContent-tilte-content-button" hover-class="popupContent-hover-button" :class="buttonDisable?'disbaled':''" :disabled="buttonDisable" @click="queClick">确定</button>
					<!-- #endif -->
				</view>
			</view-head>
			
			<view class="popupContent-content"
				<!-- #ifdef MP-WEIXIN -->
					:style="{
						<!-- height: -->
						marginTop:$getSystemInfoSync().height + 'px',
						paddingTop:'10px'
					}"
				<!-- #endif -->
			>
				<view>
					<slot></slot>
				</view>
				<!-- #ifdef MP-WEIXIN -->
				<button type="default" class="wx-popupContent-content-button" :class="buttonDisable?'disbaled':''" hover-class="wx-popupContent-hover-button" :disabled="buttonDisable" @click="queClick">确定</button>
				<!-- #endif -->
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props:{
			name:{
				type:String,
				default:"标题名字"
			},
			value:{
				type:Boolean,
			},
			buttonDisable:{
				type:Boolean
			}
		},
		data(){
			return {
				
			}
		},
		methods:{
			// 点击删除按钮关闭时间
			clearClick(e){
				this.$emit('clearClick',false);
			},
			// 点击确认按钮触发事件
			queClick(e){
				this.$emit('que-click',e)
			}
		},
		watch:{
			value(newValue){
				if(newValue){
					
				}else{
					
				}
			}
		}
	}
	
	
</script>

<style lang="scss">
	.popupContent{
		position: absolute;
		height: 100vh;
		width: 100%;
		background-color: #f0f0f0;
		top: 0;
		z-index: $uni-zindex-lg;
		
		.popupContent-tilte-content{
			position: relative;
			display: flex;
			width: 100%;	
			align-items: center;
			justify-content: space-between;
		
			.popupContent-tilte-content-button{
				display: inline-block;
				width: 140rpx;
				height: 68rpx;
				font-size: 14px;
				line-height: 68rpx;
				margin: 0;
				margin-right: 20rpx;
				background-color:#00d900;
				color: #FFFFFF;
				
			}
			
			.popupContent-tilte-content-button.disbaled{
					background-color: #989898;
			}
			
			.popupContent-hover-button{
				background-color: #00b800;
			}
			
			.popupContent-tilte-content-name{
				position: absolute;
				text-align: center;
				left: 50%;
				transform: translateX(-50%);	
			}
			
			.iconfont{
				margin: 0;
				background-color: rgba(255,255,255,0);
			}
		}
	}
	
	
	.popupContent-content{
		/* #ifdef H5 */
			margin-top: 100rpx;
		/*  #endif  */
				
	
		
		
		.wx-popupContent-content-button{
			height: 68rpx;
			font-size: 14px;
			line-height: 68rpx;
			margin:20px;
			background-color:#00d900;
			color: #FFFFFF;
		}
		
		.wx-popupContent-content-button.disbaled{
			background-color: #989898;
		}
	}
	
	
	
	
</style>
